import Dexie from 'dexie';

const collageDB = new Dexie('collage');

collageDB.version(1).stores({
  images:
    'title, src,naturalWidth,naturalHeight,aspectRatio,type,targetAspectRatio', // Primary key and indexed props
  originalImages:
    'title, src,naturalWidth,naturalHeight,aspectRatio,type,targetAspectRatio', // Primary key and indexed props
  details: '++id, details',
});

export default { collageDB };
